/* ==========================================================================
  Templates - Content
========================================================================== */

.site-main {
  margin-bottom: 2rem;
  @include breakpoint($l) {
    padding-left: 230px;
    margin-bottom: 4rem;
  }
  .home & {
    padding: 2rem 0;
    background: $grey-light;
    text-align: center;
    width: 100%;
    max-width: none;
    @include breakpoint($m) {
      padding: 4rem 0;
    }
    article {
      @include wrap;
      p:last-child {
        margin-bottom: 0;
      }
    }
  }
  .teaser {
    margin-bottom: 2rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.site-content {
  @include wrap;
  @include clearfix;
  .home & {
    width: 100%;
    max-width: none;
  }
}

.entry-meta {
  color: $grey-dark;
  margin-bottom: 1rem;
  .event-location,
  .event-date {

  }
}

.entry-footer {
  margin-bottom: 1rem;
  .cat-links,
  .tags-links {
    display: block;
    margin-bottom: 1rem;
  }
  .cat-links {

  }
  .read-more {
    color: $partio-blue;
  }
}

.post-thumbnail {
  display: block;
  img {
    vertical-align: bottom;
  }
}

.single {
  .post-thumbnail {
    margin-bottom: 1rem;
  }
  &-event {

  }
  .content-area {
    @include breakpoint($l) {
      @include grid-span(9, 1);
    }
  }
  .widget-area {
    @include breakpoint($l) {
      @include grid-span(3, 10);
    }
  }
}
