/* ==========================================================================
  Templates - Boxes
========================================================================== */

.site-boxes {
  margin-bottom: 2rem;
  @include breakpoint($m) {
    margin-bottom: 4rem;
  }
  &-wrap {
    & > h1 {
      text-align: center;
    }
    @include wrap;
    .container {
      display: flex;
      flex-direction: column;
      @include breakpoint($l) {
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
      }
      .box {
        text-align: center;
        margin-bottom: 1rem;
        @include breakpoint($l) {
          width: 32%;
          margin-bottom: 0;
        }
      }
    }
  }
}
