/* ==========================================================================
  Global - Typography
========================================================================== */

@import "../../../node_modules/sass-toolkit/stylesheets/toolkit/kickstart";

/* Typography
----------------------------------------------- */

html {
  color: $base-font-color;
  font-family: $font-text;
  line-height: $base-line-height;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 87.5%;
  @include breakpoint($m) {
    font-size: 100%;
  }
}

h1,
h2,
h3,
h4 {
  color: $partio-blue;
  font-family: $font-title;
  font-weight: $bold;
  a {
    color: inherit;
  }
}

h1, .h1 {
  font-size: 1.875rem;
  line-height: 1;
  margin: 0 0 2rem;
  text-transform: uppercase;
  @include breakpoint($m) {
    font-size: 2.125rem;
  }
}

h2, .h2 {
  font-size: 1.25rem;
  line-height: 1;
  margin: 0 0 1rem;
  padding-top: .5rem;
  @include breakpoint($m) {
    font-size: 1.5rem;
  }
}

h3, .h3 {
  font-size: 1.125rem;
  line-height: 1.5;
  margin-bottom: .5rem;
}

h4, .h4 {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: .5rem;
}

p, .p {
  font-size: 1rem;
  line-height: 1.5;
  margin: 0 0 1rem;
  &.ingress {
    font-size: 1.25rem;
  }
}

hr {
  margin: 2em 0;
  display: block;
}

/* Lists
----------------------------------------------- */

ul, ol {
  margin: 0 0 1em;
}


/* Links
----------------------------------------------- */

a {
  color: $partio-light-blue;
  text-decoration: none;
  &:hover, &:active, &:focus {
    text-decoration: underline;
  }
  &.button {
    @include button;
    &:hover {
      text-decoration: underline;
    }
  }
}


/* Forms
----------------------------------------------- */

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
  min-width: 0;
}

legend {
  display: block;
  width: 100%;
  padding: 0;
  margin-bottom: 1rem;
  border: 0;
  text-transform: none;
}

label {
  display: block;
  max-width: 100%;
  font-weight: 700;
  margin-bottom: .5rem;
}

input[type="radio"],
input[type="checkbox"] {
  margin: .25rem 0 0;
  line-height: normal;
}

select,
textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"] {
  max-width: 100%;
  padding: .5rem 1rem;
  color: $base-font-color;
  background: $white;
  min-width: 240px;
}

textarea {
  height: 180px;
  min-width: 460px;
}

button,
input[type="submit"],
input[type="button"] {
  @include button-reset;
  @include button;
}

/* Tables
----------------------------------------------- */

table, .mce-item-table {
  width: 100%;
  margin: 1.5em 0;
  border: 0;
  thead {
    background: $grey;
  }
  td {
    border: 0;
    border-left: 1px solid $grey;
    padding: .5rem .75rem;
    &:first-of-type {
      border-left: 0;
    }
  }
  tr {
    border-bottom: 1px solid $grey;
  }
}

/* Images
----------------------------------------------- */

.wp-caption {
  font-size: .9rem;
  float: none;
  margin: 0;
}

.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}

.aligncenter {
  display: block;
  margin: 1rem auto;
  height: auto;
}

.alignleft,
.alignright {
  display: block;
  margin-bottom: 1rem;
  height: auto;
}

@include breakpoint($s) {
  .alignleft {
    float: left;
    margin-right: 1rem;
  }
  .alignright {
    float: right;
    margin-left: 1rem;
  }
}

/* Gallery
----------------------------------------------- */

.gallery {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
}

.gallery-item {
  width: 100%;
  margin: 0;
  padding: 1rem 0;
  .gallery-columns-2 & {
    width: 100%;
    @include breakpoint($s) {
      width: 49%;
      margin-right: 2%;
      &:nth-of-type(2n) {
        margin-right: 0;
      }
    }
  }
  .gallery-columns-3 & {
    width: 100%;
    @include breakpoint($s) {
      width: 49%;
      margin-right: 2%;
      &:nth-of-type(2n) {
        margin-right: 0;
      }
    }
    @include breakpoint($m) {
      width: 31.9999%;
      margin-right: 2%;
      &:nth-of-type(2n) {
        margin-right: 2%;
      }
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
  }
  .gallery-columns-4 & {
    width: 100%;
    @include breakpoint($s) {
      width: 49%;
      margin-right: 2%;
      &:nth-of-type(2n) {
        margin-right: 0;
      }
    }
    @include breakpoint($m) {
      width: 31.9999%;
      margin-right: 2%;
      &:nth-of-type(2n) {
        margin-right: 2%;
      }
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
    @include breakpoint($l) {
      width: 23.5%;
      margin-right: 2%;
      &:nth-of-type(2n),
      &:nth-of-type(3n) {
        margin-right: 2%;
      }
      &:nth-of-type(4n) {
        margin-right: 0;
      }
    }
  }
}

/* Icons
----------------------------------------------- */

.icon {
  height: 1em; // match current font-size
  width:  1em; // match current font-size
  fill: currentColor;
  display: inline-block;
  vertical-align: middle;
}

/* Text format styles
----------------------------------------------- */

blockquote {
  margin-left: 0;
  padding-left: 1rem;
  border-left: 2px solid $grey;
}

iframe {
  max-width: 100%;
}

/* Accessibility
----------------------------------------------- */

a:focus {
  outline: 1px dotted;
}

.screen-reader-text {
  @include visuallyhidden;
}

.skip-to-content {
  background-color: #f1f1f1;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, .2);
  display: block;
  left: -9999em;
  outline: none;
  padding: 1rem 1.5rem;
  text-decoration: none;
  text-transform: none;
  top: -9999em;
  &:focus {
    clip: auto;
    height: auto;
    left: .5rem;
    top: .5rem;
    width: auto;
    z-index: 100000;
  }
}

button:focus,
input[type="button"]:focus,
input[type="reset"]:focus,
input[type="submit"]:focus,
.social-share-link:focus {
  outline: 1px dotted;
  outline-offset: -2px;
}
