/* ==========================================================================
  Templates - Feeds
========================================================================== */

.site-feeds {
  @include wrap;
  margin-bottom: 1rem;
  @include breakpoint($m) {
    margin-bottom: 2rem;
  }
  & > h1 {
    text-align: center;
  }
  .feed-title {
    @extend .h3;
    border-bottom: 1px solid $grey;
    margin-bottom: 1rem;
    padding-bottom: .3rem;
    .icon {
      position: relative;
      top: -2px;
    }
  }
  .container {
    display: flex;
    flex-direction: column;
    @include breakpoint($l) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    .feed-item {
      margin-bottom: 1rem;
      @include breakpoint($l) {
        margin-bottom: 2rem;
        width: 32%;
      }
      &-image {
        text-align: center;
        margin-bottom: 1rem;
        img {
          width: 100%;
          vertical-align: bottom;
        }
      }
      &-content {
        margin-bottom: 1rem;
        a {
          color: $base-font-color;
          text-decoration: underline;
          &:hover {
            text-decoration: none;
          }
        }
      }
    }
  }
}

