/* ==========================================================================
  Elements - Search
========================================================================== */

.search-form {
  position: relative;
  label {
    display: inline-block;
  }
  .search-field {
    min-width: 0;
  }
  .search-submit {
    position: relative;
    top: .175rem;
    width: 1rem;
    background: none;
    border: none;
    text-indent: -999em;
    background: url('../images/search.svg') no-repeat;
    background-size: contain;
    margin-left: -2rem;
    min-width: 0;
    padding: 0;
  }
}
