/* ==========================================================================
  Elements - Hero
========================================================================== */

.site-hero {
  @include ir(19/8, 100%, 'img');
  margin-bottom: 2rem;
  @include breakpoint($m) {
    margin-bottom: 4rem;
  }
  &-content {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    h1 {
      color: $partio-blue;
      font-size: 2.5rem;
      margin-bottom: .5rem;
      @include breakpoint($m) {
        font-size: 4rem;
      }
    }
    p {
      color: $partio-blue;
      font-size: 1.5rem;
      line-height: 1.25;
      margin-bottom: 0;
      @include breakpoint($m) {
        font-size: 1.875rem;
      }
    }
  }
}
