/* ==========================================================================
  Templates - Footer
========================================================================== */

.site-prefooter {
  background: $grey-light;
  padding: 1rem 0;
  @include clearfix;
  .container {
    @include wrap;
    @include breakpoint($l) {
      display: flex;
      align-items: center;
      height: 100%;
    }
  }
  .logo {
    display: block;
    margin-bottom: 1rem;
    @include breakpoint($l) {
      margin-right: auto;
      margin-bottom: 0;
      max-width: grid-span(2, 1);
    }
  }
}

.site-footer {
  padding: 1rem 0;
  color: $white;
  background: $partio-blue;
  border-top: solid 3px $partio-light-blue;
  @include clearfix;
  @include breakpoint($m) {
    padding: 2rem 0;
  }
  ul {
    @include list-reset;
  }
  a {
    color: inherit;
  }
  .container {
    @include wrap;
    display: flex;
    flex-direction: column;
    @include breakpoint($l) {
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
    }
  }
  .widget {
    margin-bottom: 2rem;
    @include breakpoint($l) {
      flex: 0 1 24%;
    }
  }
  .widget-title {
    @extend .h3;
    color: inherit;
    padding-top: 0;
    margin-top: 0;
  }
}
