/* ==========================================================================
  Elements - Menu toggle
========================================================================== */

.menu-toggle {
  @include button-reset;
  position: absolute;
  right: 1rem;
  top: 1rem;
  background: $partio-light-blue;
  color: $white;
  cursor: pointer;
  display: inline-block;
  text-transform: uppercase;
  transition: all .25s ease-in-out;
  font-size: 0.875em;
  padding: 1em;
  min-width: 0;
  &:hover {
    .line-1 {
      transform: translateY(-.25em);
    }
    .line-3 {
      transform: translateY(.2em);
    }
  }
  &:focus {
    outline: thin dotted;
    outline-offset: -2px;
  }
  .menu-open {
    display: block;
  }
  .menu-close {
    display: none;
  }
}

.icon-menu-toggle {
  width: 2rem;
  height: 2rem;
  top: 0;
}

.svg-menu-toggle {
  .line {
    opacity: 1;
    transform: rotate(0) translateY(0) translateX(0);
    transform-origin: 1em 1em;
    transition: transform .3s ease-in-out, opacity .2s ease-in-out;
  }
}

.menu-toggle.active {
  .menu-open {
    display: none;
  }
  .menu-close {
    display: block;
  }
}
