/* ==========================================================================
  Templates - Header
========================================================================== */

.site-header {
  background: $partio-blue;
  margin-bottom: 2rem;
  @include clearfix;
  @include breakpoint($m) {
    margin-bottom: 4rem;
  }
  .home & {
    margin-bottom: 0;
  }
  .container {
    position: relative;
    @include wrap;
    width: 100%;
    @include breakpoint($l) {
      padding-left: 230px;
    }
  }
}

.site-branding {
  float: left;
  display: block;
  padding: 1rem;
  img {
    max-width: 60px;
    vertical-align: bottom;
    @include breakpoint($l) {
      max-width: 110px;
    }
  }
  @include breakpoint($l) {
    float: none;
    position: relative;
    z-index: 10;
    background: $partio-blue;
    margin-right: auto;
    margin-bottom: 0;
    max-width: 230px;
    padding: 0 30px;
    position: absolute;
    left: 0;
    top: -105px;
    height: 230px;
    width: 230px;
    transform: rotate(45deg);
    img {
      position: absolute;
      left: 45%;
      top: 45%;
      transform: rotate(-45deg);
    }
  }
}
