/* ==========================================================================
  Global - Mixins
========================================================================== */

@mixin wrap {
  width: 95%;
  max-width: 1170px;
  margin: 0 auto;
}

@mixin list-reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

@mixin button-reset {
  border: 0;
  border-radius: 0;
  background: none;
  color: inherit;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-appearance: none; /* for input */
  -webkit-user-select: none; /* for button */
     -moz-user-select: none;
      -ms-user-select: none;
}

@mixin button {
  color: $white;
  background: $partio-blue;
  font-size: 1.125rem;
  font-weight: 700;
  display: inline-block;
  min-width: 130px;
  padding: .5rem 1em;
  border-radius: 5px;
  text-decoration: none;
  text-align: center;
}

@mixin visuallyhidden {
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  position: absolute;
}

@mixin clearfix {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}
