/* ==========================================================================
  Elements - Gallery
========================================================================== */

.gallery {
  @include breakpoint($m) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex-direction: row;
    justify-content: space-between;
    .gallery-item {
      width: 49%;
    }
  }
  @include breakpoint($l) {
    &.gallery-columns-1 {
      .gallery-item {
        width: 100%;
      }
    }
    &.gallery-columns-2 {
      .gallery-item {
        width: 49%;
      }
    }
    &.gallery-columns-3 {
      .gallery-item {
        width: 32%;
      }
    }
    &.gallery-columns-4 {
      .gallery-item {
        width: 24%;
      }
    }
    &.gallery-columns-5 {
      .gallery-item {
        width: 19%;
      }
    }
  }
  .gallery-item {
    padding: .3rem;
    margin: 0 0 1rem;
    border: 1px solid $grey;
    background: $white;
    &:hover {
      img {
        filter: brightness(80%);
      }
    }
    img {
      width: 100%;
      height: auto;
      vertical-align: bottom;
      transition: filter .2s ease-in;
    }
  }
}
