/* ==========================================================================
  Global - Site variables
========================================================================== */

/* Fonts
----------------------------------------------- */

$font-text: 'PT Sans', sans-serif;
$font-title: 'Source Sans Pro', sans-serif;

$base-line-height: 1.6;
$base-font-color: #202020;

$normal: 400;
$bold: 700;


/* Colors
----------------------------------------------- */

$black: #000;
$white: #fff;
$grey: #ddd;
$grey-light: #f4f5f7;
$grey-dark: #555;

$partio-blue: #253765;
$partio-light-blue: #28a9e1;

$blue-facebook: #3b5998;
$blue-twitter: #00aced;
$blue-linkedin: #007bb6;
$red-google-plus: #dd4b39;
$red-pinterest: #cb2027;


/* Breakpoints
----------------------------------------------- */

$sub-s: max-width 529px;
$s: 530px, 'no-query' '.lt-ie9';
$sub-m: max-width 719px;
$m: 720px, 'no-query' '.lt-ie9';
$sub-l: max-width 979px;
$l: 980px, 'no-query' '.lt-ie9';

$menu-hidden: $sub-l;
$menu-visible: $l;
$menu-dropdown-width: 15rem;

/* Grid settings
----------------------------------------------- */

@include add-grid(4);
@include add-grid(12 at $m);
@include add-gutter(0.5);
