/* ==========================================================================
  Elements - Social share buttons
========================================================================== */

.social-share-container {
  margin: 2rem 0 4rem;
  .social-share-link {
    display: inline-block;
    color: $white;
    vertical-align: top;
    padding: .5rem;
    line-height: 0;
    margin: 0 .25rem .5rem 0;
    position: relative;
    transition: background .2s ease-in-out;
    text-decoration: none;
    .icon-wrap {

    }
    .icon {
      font-size: 1.875rem;
    }
    span {
      &.social-share-service {
        display: none;
      }
    }
    &:last-of-type {
      margin-right: 0;
    }
    &.social-share-fb {
      background: $blue-facebook;
      &:hover, &:focus {
        background: darken($blue-facebook, 9);
      }
    }
    &.social-share-twitter {
      background: $blue-twitter;
      &:hover, &:focus {
        background: darken($blue-twitter, 9);
      }
    }
  }
}
