/* ==========================================================================
  Templates - Latest posts
========================================================================== */

.site-latest {
  background: $partio-light-blue;
  padding: 2rem 0;
  @include breakpoint($l) {
    padding: 4rem 0;
  }
  &-wrap {
    @include wrap;
    & > h1 {
      color: #fff;
      text-align: center;
    }
    .container {
      display: flex;
      flex-direction: column;
      @include breakpoint($l) {
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
      }
      article {
        background: #fff;
        padding: 5px;
        margin: 0 auto;
        margin-bottom: 1rem;
        max-width: 380px;
        @include breakpoint($l) {
          width: 32%;
          margin-bottom: 0;
        }
        .entry-header,
        .entry-content,
        .entry-footer {
          padding: 0 1rem;
        }
      }
    }
  }
  &-footer {
    text-align: center;
    margin-top: 2rem;
    @include breakpoint($l) {
      margin-top: 4rem;
    }
  }
}

.blog {
  .site-content {
    @include clearfix;
  }
  .content-area {
    @include breakpoint($l) {
      @include grid-span(9, 1);
    }
  }
  .widget-area {
    @include breakpoint($l) {
      @include grid-span(3, 10);
    }
  }
}
