/* ==========================================================================
  Elements - Primary menu
========================================================================== */

.main-navigation {
  position: relative;
  z-index: 25;
}

button.menu-toggle {
  display: none;
  @include breakpoint($menu-hidden) {
    display: inline-block;
  }
}

#primary-menu {

  /* -------------------------------------------------------
    Both navigations
  ---------------------------------------------------------*/

  a {
    color: #fff;
    font-weight: $bold;
  }

  &, ul {
    @include list-reset;
  }

  /* -------------------------------------------------------
    Mobile navigation
  ---------------------------------------------------------*/

  @include breakpoint($menu-hidden) {
    display: none;
    clear: both;
    background: $partio-light-blue;
    .active & {
      display: block;
    }

    // all <li>
    li {
      &.menu-item-has-children {
        position: relative;
        & > a {
          display: block;
          margin-right: 3rem; // room for caret
        }

        & > .icon-wrap {
          color: #fff;
          position: absolute;
          right: 1rem;
          top: .75rem;
        }

        svg {
          transition: transform .2s ease-out;
        }

        &.active {
          & > .icon-wrap > .icon {
            transform: rotate(180deg); // flip caret to point up
          }
        }

      }
    }

    // all <a>
    a {
      display: block;
      padding: 1rem;
      text-decoration: none;
    }

    // all .sub-menu
    .sub-menu {

    }

    // 1st level <li>
    & > li {
      border-bottom: 1px solid #fff;
      text-transform: uppercase;
      &:last-child {
        border-bottom: none;
      }

      // 1st level <a>
      & > a {

      }

        // 2nd level <ul class="sub-menu"> [closed]
        & > ul {
          max-height: 0;
          background: #fff;
          opacity: 0;
          transition: all .2s ease-out;

          // 2nd level <ul class="sub-menu"> [open]
          &.open {
            opacity: 1;
            max-height: 999px;
          }

          // 2nd level <li>
          & > li {
            border-bottom: 1px solid $partio-blue;
            padding-left: 1.5rem;
            &:last-child {
              border-bottom: none;
            }

            &.menu-item-has-children {
              & > .icon-wrap {
                color: $partio-blue;
              }
            }

            // 2nd level <a>
            & > a {
              color: $partio-blue;
            }

            // 3rd level <ul class="sub-menu"> [closed]
            & > ul {
              max-height: 0;
              opacity: 0;
              transition: all .2s ease-out;

              // 3rd level <ul class="sub-menu"> [open]
              &.open {
                opacity: 1;
                max-height: 999px;
              }
              & > li {
                text-transform: none;
                padding-left: 1.5rem;
                & > a {
                  color: $partio-blue;
                }
              }
            }


          }

        }

    }


  }

  /* -------------------------------------------------------
    Desktop navigation
  ---------------------------------------------------------*/

  @include breakpoint($menu-visible) {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 25;

    // all <li>
    li {

    }

    // all <a>
    a {
      text-decoration: none;
    }

    // 1st level <li>
    & > li {
      display: block;
      position: relative;
      padding: 1rem 1.5rem;
      transition: all .1s;
      cursor: pointer;
      .blog &.latest,
      .single-post &.latest,
      .blog &.latest,
      .single-event &.events,
      &.current-menu-item,
      &.current_page_item,
      &:hover {
        background: $partio-light-blue;
      }
      &:last-of-type {
        margin-right: 0;
      }
      .icon-wrap {
        position: relative;
        top: -1px;
        color: #fff;
        margin-left: .5rem;
      }

      // 1st level <a>
      & > a {

      }

      // 2nd level <ul class="sub-menu"> [open]
      &:hover > .sub-menu,
      & > .sub-menu.open {
        opacity: 1;
        left: 0;
      }

      // 2nd level <ul class="sub-menu"> [closed]
      & > ul {
        width: $menu-dropdown-width;
        margin: 1rem 0 0 0; // margin-left half of the width!
        background: $partio-light-blue;
        position: absolute;
        left: -999em;
        z-index: 99;
        // box-shadow: 0 5px 15px 0 rgba(0,0,0,.15);
        opacity: 0;
        transition: opacity .2s ease-out;

        // 2nd level <li>
        & > li {
          padding: 1rem 1.5rem;
          &:hover {
            text-decoration: underline;
            background: darken($partio-light-blue, 5%);
          }
          &.menu-item-has-children {
            position: relative;
            a {
              margin-right: 1.5rem;
            }
            & > .icon-wrap {
              position: absolute;
              right: 1rem;
              top: 1rem;
              & > .icon {
                transform: rotate(-90deg);
              }
            }
          }

          // 2nd level <a>
          & > a {
            display: block;
          }

          // 3rd level <ul class="sub-menu"> [open]

          &:hover > .sub-menu,
          & > .sub-menu.open {
            opacity: 1;
            left: $menu-dropdown-width;
          }

          // 3rd level <ul class="sub-menu"> [closed]
          & > ul {
            width: $menu-dropdown-width;
            background: #fff;
            border-left: 5px solid $partio-blue;
            position: absolute;
            top: 0;
            left: -999em;
            z-index: 99;
            // box-shadow: 0 5px 15px 0 rgba(0,0,0,.15);
            opacity: 0;
            transition: opacity .2s ease-out;
            & > li {
              padding: 1rem;
              &:hover {
                text-decoration: underline;
              }
              & > a {
                color: $partio-blue;
              }
            }
          }

        }

      }

    }
  }
}
